.profile-container {
  background-color: white;
  min-height: 450px;
  width: 250px;
  border-radius: 15px;
  box-shadow: 0px 2px 10px 2px black;
  margin: 10px 10px;
  transition: transform 0.3s ease;
  margin-top: 50px;
}

.profile-container:hover {
  transform: translateY(-15px);
}
