:root {
  /*page vars*/
  --clr-auth-background-dark: #046364;
  --clr-auth-background-light: #0aeef2;

  --box-shadow: 15px 15px 10px 0 rgba(0, 0, 0, 0.4);
  --inputfields-bg: rgba(255, 255, 255, 0.15);
  --input-gap: 1.5em;
}

* {
  -ms-overflow-style: none;
}
::-webkit-scrollbar {
  display: none;
}

.auth {
  background: linear-gradient(to bottom, var(--clr-auth-background-dark), var(--clr-auth-background-light));
  min-height: 100vh;
  padding-bottom: 20px;
  display: flex;
  justify-content: center;
}

h3 {
  text-align: center;
  font-family: "Lobster", sans-serif;
  font-size: 3rem;
}

.form {
  margin: 0 auto;
  width: 45%;
  padding-top: 10vh;
}

.inputfields {
  padding: 30px 70px;
  box-shadow: var(--box-shadow);
  border-radius: 20px;
  background-color: var(--inputfields-bg);
  backdrop-filter: blur(5px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: var(--input-gap);
}

.anticon {
  transform: scale(1.5);
}

div.ant-form-item {
  display: flex;
  flex-direction: column;
  margin-bottom: 0;
  align-items: flex-start;
  width: 65%;
}

div.ant-form-item-control {
  width: 100%;
}

/* Vertical responsiveness */
@media (max-width: 540px) {
  .form {
    width: 90%;
  }
  h3 {
    padding-top: 80px;
  }
}

@media (max-width: 360px) {
  h3 {
    padding-top: 80px;
  }
}

@media (max-width: 320px) {
  h3 {
    padding-top: 50px;
  }
}

@media (min-width: 375px) and (min-height: 670px) {
  h3 {
    padding-top: 80px;
  }
}

@media (min-width: 400px) and (min-height: 800px) {
  h3 {
    padding-top: 150px;
  }
}

@media (min-width: 750px) and (min-height: 1024px) {
  .form {
    width: 90%;
  }
}

@media (min-width: 1024px) and (min-height: 1366px) {
  h3 {
    padding-top: 300px;
  }
}

/* Landscape responsiveness */
@media (max-height: 360px) and (max-width: 640px) {
  h3 {
    padding-top: 80px;
    font-size: 1.5em;
  }
  .form {
    width: 90%;
  }
  .inputfields {
    padding-top: 15px;
  }
}

@media (max-height: 540px) and (max-width: 825px) {
  h3 {
    padding-top: 80px;
  }
  .form {
    width: 90%;
  }
}
